.App {
  text-align: center;
  color: #ffffff;
  background-color: #282c34;
}

.App-logo {
  height: 10vmin;
}

.image-gallery-slide, ::after, ::before {
  background-color: #282c34;
}

.image-gallery-slide img {
  width: 600px;
  height: 600px;
}

.App-page{
  padding-bottom: 5%;
}

#modal-size{
  max-width: 90%;
  width:80%;
}

.header-title{
  color:#ffffff;
  font-size: 60%;
}

.App-gallery{
  background-color: #282c34;
  padding-bottom: 5%;
}

.modal-body img{
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.modal-body button{
  float: right;
}

.form-control {
  width: 15%;
  margin-left: auto;
  margin-right: auto;
}

.App-form {
  padding-bottom: 5%;
}

.btn-outline-primary {
  color: #ffffff;
  border-color: #ffffff;
}

.btn-outline-primary:hover {
  color: #000000;
  background-color: #ffffff;
  border-color: #ffffff;
}

.App-header {
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  background-color: #282c34;
}

