body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  color: #ffffff;
  background-color: #282c34;
}

.App-logo {
  height: 10vmin;
}

.image-gallery-slide, ::after, ::before {
  background-color: #282c34;
}

.image-gallery-slide img {
  width: 600px;
  height: 600px;
}

.App-page{
  padding-bottom: 5%;
}

#modal-size{
  max-width: 90%;
  width:80%;
}

.header-title{
  color:#ffffff;
  font-size: 60%;
}

.App-gallery{
  background-color: #282c34;
  padding-bottom: 5%;
}

.modal-body img{
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.modal-body button{
  float: right;
}

.form-control {
  width: 15%;
  margin-left: auto;
  margin-right: auto;
}

.App-form {
  padding-bottom: 5%;
}

.btn-outline-primary {
  color: #ffffff;
  border-color: #ffffff;
}

.btn-outline-primary:hover {
  color: #000000;
  background-color: #ffffff;
  border-color: #ffffff;
}

.App-header {
  min-height: 10vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  background-color: #282c34;
}

.css-42igfv {
  color: #ffffff;
}

.loadingSpinner {
  color: black;
}

.css-50etie svg {
  background-color: transparent;
}

.App-link {
  color: #61dafb;
}

